html {
	scroll-behavior: smooth;
	box-sizing: border-box;
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

body {
	letter-spacing: 1.7;
	color: $dark-gray;
	margin: 0;
	padding: 0;

	* {
		margin: 0;
		padding: 0;
	}
}

section {
	padding-top: $nav-height;
}

h1 {
	font-size: 2rem;
	line-height: 1;
}

p,
li {
	line-height: 1.6;
}

a {
	color: $blue;
	text-decoration: none;
}

.sr-only:not(:focus):not(:active) {
	clip: rect(0 0 0 0);
	clip-path: inset(50%);
	height: 1px;
	overflow: hidden;
	position: absolute;
	white-space: nowrap;
	width: 1px;
}